'use client';

import { Button } from "@heroui/react";
import Link from "next/link";
import { useParams } from "next/navigation";
import { useUserStore } from "../../../../stores/user/userStore";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";

// Validation Schema
const userSchema = Yup.object().shape({
    name: Yup.string()
        .min(2, 'Name must be at least 2 characters')
        .required('Name is required'),
    email: Yup.string()
        .email('Invalid email format')
        .required('Email is required'),
    role: Yup.string()
        .required('Role is required'),
    status: Yup.string()
        .required('Status is required')
});

export default function EditUserPage() {
    const params = useParams();
    const userId = params.id as string;
    const { users, updateUser } = useUserStore();
    const user = users.find(u => u.id === userId);

    if (!user) {
        return (
            <div className="min-h-screen bg-black text-white p-6">
                <div className="text-center">
                    <h1 className="text-2xl font-bold mb-4">User Not Found</h1>
                    <Link href="/user">
                        <Button>Back to Users</Button>
                    </Link>
                </div>
            </div>
        );
    }

    const initialValues = {
        name: user.name,
        email: user.email,
        role: user.role,
        status: user.status
    };

    const handleSubmit = (values: typeof initialValues) => {
        updateUser(userId, {
            name: values.name,
            email: values.email,
            role: values.role,
            status: values.status as 'Active' | 'Inactive'
        });
        window.location.href = `/user`;
    };

    return (
        <div className="min-h-screen bg-black text-white p-6">
            {/* Header */}
            <div className="flex items-center justify-between mb-8">
                <div>
                    <h1 className="text-2xl font-bold mb-1">Edit User</h1>
                    <p className="text-gray-400 text-sm">Update user information</p>
                </div>
                <Link href={`/user/${userId}`}>
                    <Button className="border-zinc-700">
                        ‚Üê Back to Details
                    </Button>
                </Link>
            </div>

            {/* Form */}
            <div className="bg-zinc-900 rounded-lg border border-zinc-800 p-6 max-w-2xl">
                <Formik
                    initialValues={initialValues}
                    validationSchema={userSchema}
                    onSubmit={handleSubmit}
                >
                    {({ errors, touched }) => (
                        <Form className="space-y-6">
                            {/* Full Name */}
                            <div>
                                <label htmlFor="name" className="text-sm text-gray-400 block mb-2">
                                    Full Name <span className="text-red-500">*</span>
                                </label>
                                <Field
                                    type="text"
                                    id="name"
                                    name="name"
                                    placeholder="Enter full name"
                                    className={`w-full bg-black border ${errors.name && touched.name ? 'border-red-500' : 'border-zinc-800'} rounded-lg px-4 py-2.5 text-sm focus:outline-none focus:border-zinc-700`}
                                />
                                <ErrorMessage name="name" component="p" className="text-red-500 text-xs mt-1" />
                            </div>

                            {/* Email */}
                            <div>
                                <label htmlFor="email" className="text-sm text-gray-400 block mb-2">
                                    Email Address <span className="text-red-500">*</span>
                                </label>
                                <Field
                                    type="email"
                                    id="email"
                                    name="email"
                                    placeholder="user@oilgas.com"
                                    className={`w-full bg-black border ${errors.email && touched.email ? 'border-red-500' : 'border-zinc-800'} rounded-lg px-4 py-2.5 text-sm focus:outline-none focus:border-zinc-700`}
                                />
                                <ErrorMessage name="email" component="p" className="text-red-500 text-xs mt-1" />
                            </div>

                            {/* Role */}
                            <div>
                                <label htmlFor="role" className="text-sm text-gray-400 block mb-2">
                                    Role <span className="text-red-500">*</span>
                                </label>
                                <Field
                                    as="select"
                                    id="role"
                                    name="role"
                                    className="w-full bg-black border border-zinc-800 rounded-lg px-4 py-2.5 text-sm focus:outline-none focus:border-zinc-700"
                                >
                                    <option value="Administrator">Administrator</option>
                                    <option value="Field Supervisor">Field Supervisor</option>
                                    <option value="Engineer">Engineer</option>
                                </Field>
                            </div>

                            {/* Status */}
                            <div>
                                <label htmlFor="status" className="text-sm text-gray-400 block mb-2">
                                    Status <span className="text-red-500">*</span>
                                </label>
                                <Field
                                    as="select"
                                    id="status"
                                    name="status"
                                    className="w-full bg-black border border-zinc-800 rounded-lg px-4 py-2.5 text-sm focus:outline-none focus:border-zinc-700"
                                >
                                    <option value="Active">Active</option>
                                    <option value="Inactive">Inactive</option>
                                </Field>
                            </div>

                            {/* Info Box */}
                            <div className="bg-zinc-800/50 border border-zinc-700 rounded-lg p-4">
                                <p className="text-sm text-gray-400">
                                    <strong className="text-white">User ID:</strong> {user.id}
                                </p>
                                <p className="text-sm text-gray-400 mt-1">
                                    <strong className="text-white">Created Date:</strong> {user.createdDate}
                                </p>
                            </div>

                            {/* Actions */}
                            <div className="flex gap-3 pt-6 border-t border-zinc-800">
                                <Button
                                    type="submit"
                                    className="bg-blue-600 hover:bg-blue-700 text-white font-medium px-8"
                                >
                                    Update User
                                </Button>
                                <Link href={`/user`}>
                                    <Button
                                        type="button"
                                        className="border-zinc-700"
                                    >
                                        Cancel
                                    </Button>
                                </Link>
                            </div>
                        </Form>
                    )}
                </Formik>
            </div>
        </div>
    );
}
